import echarts from 'echarts'
import { isOpenDatabase, openCommDB, executeSQL, selectSQL } from '../../util/sqlLiteUtil.js'
import {queryAll,getPatrolTemplateType,getPatrolTemplate} from '../../api/user.js'
import {setToken, getToken ,setInspectionModal, getByKey ,setByKey} from '../../util/auth.js'
import TOKEN_MESSAGE_CONTANTS from '../../constant/commonConstant.js'
import {isNullOrEmpty,objIsNullOrEmpty,getUUID} from '../../util/stringUtil.js'
export default {
	data() {
		return {
			TabCur: 0,
			scrollLeft: 0,
			approvalCur:0,
			workCur:0,
			goodsCur:0,
			tabName: ['采购审批', '询价审批', '出库审批', '基建审批'],
			index: null,
			orgOptions: {},
			sumValue: 1000
		}
	},
	onLoad(option) {
		if(!isNullOrEmpty(option.tabCur)){
			this.TabCur = option.tabCur
		}
	},
	methods: {
		changeOption() {
			const data = this.option.series[0].data
			// 随机更新示例数据
			data.forEach((item, index) => {
				data.splice(index, 1, Math.random() * 40)
			})
		},
		onViewClick(options) {
			console.log(options)
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		kpiSelect(e){
			this.kapCur = e.currentTarget.dataset.id;
		},
		showModal(e) {
			this.modalName = 'DialogModal1'
		},
		hideModal(e) {
			this.modalName = null
		},
		approvalUrl(){
			if(this.TabCur==0){
				uni.navigateTo({
				    url: 'purchaseApproval'
				});
			}
			if(this.TabCur==1){
				uni.navigateTo({
				    url: 'enquiryApproval'
				});
			}
			if(this.TabCur==2){
				uni.navigateTo({
				    url: 'deliveryApproval'
				});
			}
			if(this.TabCur==3){
				uni.navigateTo({
				    url: 'constructionApproval'
				});
			}
		}
	}
}
